import {Dispatch, FC, SetStateAction} from "react";
import NavigationBar from "../components/NavigationBar";
import {Avatar, Box, Button, Container, CssBaseline, TextField, Theme, ThemeProvider, Typography} from "@mui/material";
import ContactMailIcon from '@mui/icons-material/ContactMail';


interface Props {
    toggleDarkMode: boolean;
    setToggleDarkMode: Dispatch<SetStateAction<boolean>>;
    theme: Theme
}

const ContactPage: FC<Props> = ({toggleDarkMode, setToggleDarkMode, theme}) => {
    const content = (
        <ThemeProvider theme={theme}>
            <Container component="main" maxWidth="xs" sx={{marginTop: "100px"}}>
                <CssBaseline />
                <Box
                    sx={{
                        marginTop: 8,
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                    }}
                >
                    <Avatar sx={{ m: 1, bgcolor: 'primary.main' }}>
                        <ContactMailIcon />
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Contact me
                    </Typography>
                    <Box component="form" onSubmit={(e) => e.preventDefault()} noValidate sx={{ mt: 1 }}>
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            id="email"
                            label="Email Address"
                            name="email"
                            autoComplete="email"
                            autoFocus
                        />
                        <TextField
                            margin="normal"
                            required
                            fullWidth
                            name="message"
                            label="Message"
                            type="message"
                            id="message"
                            rows={4}
                            autoComplete="current-password"
                        />
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{ mt: 3, mb: 2, bgcolor: 'primary.main' }}
                        >
                            Submit
                        </Button>
                    </Box>
                </Box>
            </Container>
        </ThemeProvider>
    );


    return (
        <NavigationBar
            toggleDarkMode={toggleDarkMode}
            setToggleDarkMode={setToggleDarkMode}
            content={content}
            fullScreen={true}
        />
    )
};

export default ContactPage;