import React, {Dispatch, FC, SetStateAction} from "react";
import NavigationBar from "../components/NavigationBar";
import {
    Box,
    Button,
    Container,
    CssBaseline,
    Paper,
    Step,
    StepLabel,
    Stepper,
    Theme,
    ThemeProvider,
    Typography
} from "@mui/material";
import Education from "../components/Education";
import WorkExperience from "../components/WorkExperience";
import SchoolIcon from "@mui/icons-material/School";
import WorkIcon from "@mui/icons-material/Work";

interface Props {
    toggleDarkMode: boolean;
    setToggleDarkMode: Dispatch<SetStateAction<boolean>>;
    theme: Theme
}

const steps = [{text: 'Education', icon: SchoolIcon}, {text: 'Work experience', icon: WorkIcon}];

function getStepContent(step: number) {
    switch (step) {
        case 0:
            return <Education/>;
        case 1:
            return <WorkExperience/>;
        default:
            throw new Error('Unknown step');
    }
}

const PersonalInformationPage: FC<Props> = ({toggleDarkMode, setToggleDarkMode, theme}) => {
    const [activeStep, setActiveStep] = React.useState(0);

    const handleNext = () => {
        setActiveStep(activeStep + 1);
    };

    const handleBack = () => {
        setActiveStep(activeStep - 1);
    };

    const content = (
        <ThemeProvider theme={theme}>
            <CssBaseline/>
            <Container component="main" maxWidth="sm" sx={{mb: 4, marginTop: "100px"}}>
                <Paper variant="outlined" sx={{my: {xs: 3, md: 6}, p: {xs: 2, md: 3}}}>
                    <Typography component="h1" variant="h4" align="center">
                        Personal Information
                    </Typography>
                    <Stepper activeStep={activeStep} sx={{pt: 3, pb: 5}}>
                        {steps.map((label) => (
                            <Step key={label.text}>
                                <StepLabel StepIconComponent={label.icon}>{label.text}</StepLabel>
                            </Step>
                        ))}
                    </Stepper>
                    <React.Fragment>
                        {getStepContent(activeStep)}
                        <Box sx={{display: 'flex', justifyContent: 'flex-end'}}>
                            {activeStep !== 0 && (
                                <Button variant="contained" onClick={handleBack} sx={{mt: 3, ml: 1}}>
                                    Back
                                </Button>
                            )}
                            {
                                activeStep === steps.length - 1 ? undefined :
                                    <Button
                                        variant="contained"
                                        onClick={handleNext}
                                        sx={{mt: 3, ml: 1}}
                                    >
                                        Next
                                    </Button>
                            }
                        </Box>
                    </React.Fragment>
                </Paper>
            </Container>
        </ThemeProvider>
    );

    return (
        <NavigationBar
            toggleDarkMode={toggleDarkMode}
            setToggleDarkMode={setToggleDarkMode}
            content={content}
            fullScreen={true}
        />
    )
}

export default PersonalInformationPage;